/*路由配置项*/
module.exports = {
  routes: [
    {
      path: '/',
      component: require('./page/phone/phone.vue')
    },
    {
      path: '/home',
      component: require('./page/home.vue')
    },
    {
      name:'phone',
      path: '/phone',
      component: require('./page/phone/phone.vue')
    },
    {
      name: 'filter',
      path: '/filter',
      component: require('./page/phone/filter.vue')
    },
    {
      name: 'phoneDetails',
      path: '/phoneDetails',
      component: require('./page/phone/phoneDetails.vue')
    }
  ],
  scrollBehavior (to, from, savedPosition) {
      return {x: 0, y: 0}
  }
}



