var judgeState=function(state){
  switch (state) {
    case 1:
      return "正常"
      break;
    case 2:
      return "遗失"
      break;
    case 3:
      return "损坏"
      break;
    case 4:
      return "报废"
      break;
    default:
      return "正常"
  }
}
export default {
    judgeState:judgeState
}



