<template>
  <div>
    <el-input placeholder="搜索一下,你就知道" icon="search" v-model="key" @input="test()"></el-input>
    <div>
      <el-row class="pad-bt05 box v-c">
        <el-col :span="12" class="box v-c">
          <h3>手机</h3>
        </el-col>
        <el-col :span="6" class="tx-r">
          <el-button class="btn" type="danger" @click="clear()" v-show="filterBol">取消筛选</el-button>
        </el-col>
        <el-col :span="6" class="tx-r">
          <router-link to="/filter">
            <el-button class="btn" type="warning">筛选</el-button>
          </router-link>
        </el-col>
      </el-row>
      <div v-show="filterBol" class="pad-b05">筛选条件
      <span v-for="(val,index) in filterCondition" class="span1">
        {{val}}
      </span>
      </div>
    </div>
    <div class="device_list" v-loading="loading" element-loading-text="拼命加载中">
      <el-row>
        <el-col :span="8" class="device_item"
                v-for="(val,index) in device_list"
                v-show="search(val)">
          <router-link :to="{name:'phoneDetails',query:{index:index}}">
            <div class="box h-c">
              <div class="device iphone7"></div>
            </div>
            <p>{{val.phoneVersion}}</p>
            <h4>{{val.osVersion.indexOf('a')!='-1'?"安卓 ":"iOS "}}{{val.osVersion.substring(1)}}</h4>
            <h5>{{val.owner}}/{{val.location}}/{{val.state|judgeState}}</h5>
          </router-link>
        </el-col>
      </el-row>
    </div>
    <div v-show="noResult" class="tx-c pad-t05">
      <p class="fz1-2 t-ff0">没有找到符合筛选条件的手机</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {mapState, mapMutations, mapActions} from 'vuex'
  export default{
    data(){
      return {
        loginDialog: true,
        noResult: false,
        key: "",
        device_list: [],
        loading: false,
        filterBol: false,
        filterCondition: []
      }
    },
    created(){
      let dl = this.$store.state.device_list.length;
      if (dl == 0) {
        this.getPhoneList();
      } else {
        if (this.filterBol) {
          let searchParams = this.handleObj(this.filters);
          this.getPhoneList(searchParams);
        }
      }
    },
    methods: {
      getPhoneList(params){
        console.log("开始向后台请求数据");
        if (params == undefined) {
          params = {};
        }
        this.loading = true;
        this.$http.jsonp(this.config.host + "/phoneSearch.do", {
          jsonp: 'jsoncallback',
          params: params
        }).then((response) => {
          // 响应成功回调
          this.loading = false;
          //console.log(data.data.result);
          if (response.data.result != null) {
            this.noResult = false;
            let dres = response.data.result;
            this.device_list = dres;
            this.$store.state.device_list = dres;
            if (!this.filterBol) {
              sessionStorage.setItem("dlist", JSON.stringify(dres));
            }
          } else {
            this.$store.state.device_list = [];
            sessionStorage.clear("dlist");
            this.noResult = true;
          }
        }, (response) => {
          // 响应错误回调
          this.loading = false;
        })
      },
      search(obj){
        var key1 = this.keywords;
        var arr1 = [];
        for (var i in obj) {
          if (obj[i]) {
            arr1.push(obj[i]);
          }
        }
        return arr1.some(function (item) {
          //搜索时不区分大小写
          return item.toString().toUpperCase().indexOf(key1.toUpperCase()) != -1;
        })
      },
      test(){
        this.$store.state.keywords = this.key;
      },
      handleObj(obj){
        let searchParams = {};
        for (var key in obj) {
          if (obj[key] !== "") {
            searchParams[key] = obj[key];
            if (key == 'state') {
              this.filterCondition.push(this.judgeState(obj[key]));
            } else {
              this.filterCondition.push(obj[key]);
            }
          }
        }
        return searchParams;
      },
      clear(){
        this.clearFilters();
        this.getPhoneList();
      },
      judgeState(state){
        this.util.judgeState(state);
      },
      ...mapActions([
        'incrementCount',
        'clearFilters'
      ])
    },
    filters: {
      judgeState: function (state) {
        switch (state) {
          case 1:
            return "正常"
            break;
          case 2:
            return "遗失"
            break;
          case 3:
            return "损坏"
            break;
          case 4:
            return "报废"
            break;
          default:
            return "正常"
        }
      }
    },
    computed: mapState([
      // 映射 this.keywords 为 store.state.keywords
      'keywords',
      'filters',
      'filterBol',
      'device_list'
    ])
  }
</script>



