<template>
  <div>
    <div>
      <el-row class="pad-bt05 box v-c">
        <el-col :span="12">
          <router-link to="/phone">
            <el-button class="btn" type="danger">返回</el-button>
          </router-link>
        </el-col>
        <el-col :span="12" class="tx-r">

        </el-col>
      </el-row>
      <h3>筛选条件</h3>
      <div class="condition box v-c">
        <span class="c_name">系统</span>
        <el-radio-group v-model="filters.osCategory" @change="filter('osCategory',$event)">
          <el-radio-button label="iOS"></el-radio-button>
          <el-radio-button label="Android"></el-radio-button>
        </el-radio-group>
      </div>
      <!--<div class="condition box v-c">-->
      <!--<span class="c_name">系统版本</span>-->
      <!--<el-radio-group v-model="filters.osVersion">-->
      <!--<el-radio-button label="8.1"></el-radio-button>-->
      <!--<el-radio-button label="10"></el-radio-button>-->
      <!--</el-radio-group>-->
      <!--</div>-->
      <div class="condition box v-c">
        <span class="c_name">所在地</span>
        <el-radio-group v-model="filters.location" @change="filter('location',$event)">
          <el-radio-button label="上海"></el-radio-button>
          <el-radio-button label="武汉"></el-radio-button>
        </el-radio-group>
      </div>
      <div class="condition box v-c">
        <span class="c_name">状态</span>
        <el-radio-group v-model="filters.state" @change="filter('state',$event)">
          <el-radio-button label="1">正常</el-radio-button>
          <el-radio-button label="2">遗失</el-radio-button>
          <el-radio-button label="3">损坏</el-radio-button>
          <el-radio-button label="4">报废</el-radio-button>
        </el-radio-group>
      </div>
      <div class="mar-t1">
        <el-row>
          <el-col :span="12" class="tx-c">
            <router-link to="/phone">
              <el-button type="success">确认筛选</el-button>
            </router-link>
          </el-col>
          <el-col :span="12" class="tx-c">
            <el-button type="warning" @click="clearFilter()">重置筛选</el-button>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import {mapState, mapActions} from 'vuex'
  export default{
    data(){
      return {
        loading:false,
      }
    },
    created(){
      this.getFilterList();
    },
    methods: {
      getFilterList(){
        this.loading = true;
        //获取筛选条件
        this.$http.jsonp(this.config.host + "/phoneFilterList.do", {
          jsonp: 'jsoncallback'
        }).then(function (response) {
          //console.log(response.data);
        }, function (response) {
          this.loading = false;
        })
      },
      filter(key, val){
        this.conditionFilter({'condition': key, 'value': val});
      },
      clearFilter(){
        this.clearFilters();
      },
      ...mapActions([
        'conditionFilter',
        'clearFilters'
      ])
    },
    computed: mapState([
      'filters'
    ])
  }
</script>



