<template>
  <div id="app" element-loading-text="登录中" v-loading="loading">
    <el-dialog title="登录" v-model="dialogFormVisible" size="large">
      <el-form :model="form">
        <el-alert :title="errorMsg" type="error" v-show="errorBol"></el-alert>
        <el-form-item label="用户名">
          <el-input v-model="form.username" auto-complete="off" @input="clearError()"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password" type="password" auto-complete="off" @input="clearError()"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-row>
          <el-col :span="12" class="box h-c">
            <el-button @click="cancel()">取 消</el-button>
          </el-col>
          <el-col :span="12" class="box h-c">
            <el-button type="primary" @click="login()">确 定</el-button>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
    <div :class="{rightSlide:showSide,leftSlide:!showSide}">
      <!-- 头部导航 -->
      <header class="header box v-c">
        <div class="f1 top_list">
          <el-row>
            <el-col :span="4">
              <div class="box v-c">
              <span class="el-dropdown-link">
                  <el-button class="btn" type="success" @click="showSide = !showSide">侧边栏</el-button>
              </span>
              </div>
            </el-col>
            <el-col :span="16">
              <div class="top_nav">设备</div>
            </el-col>
            <el-col :span="4">
              <div class="box v-c pull-right">
                  <span class="el-dropdown-link">
                    <el-button class="btn" type="primary" @click="dialogFormVisible=true" v-show="!loginUserName">登录
                    </el-button>
                    <router-link to="/phone">
                      <el-button class="btn" type="text" @click="exitLogin()" v-show="loginUserName">退出登录
                      </el-button>
                    </router-link>
                  </span>
              </div>
            </el-col>
          </el-row>
        </div>
      </header>
      <div class="main_content">
        <router-view></router-view>
      </div>
    </div>
    <div class="sidebar" @click="showSide = !showSide" :class="{rightSlide:showSide,leftSlide:!showSide}">
      <el-row class="tac tx-c">
        <h4>QA</h4>
        <el-col :span="36" class="box v-c">
          <el-menu mode="vertical" default-active="1" class="el-menu-vertical-demo">
            <el-menu-item-group>
              <el-menu-item index="2">测试中心</el-menu-item>
              <router-link to="/phone">
                <el-menu-item index="1">设备</el-menu-item>
              </router-link>
              <el-menu-item index="3">读书</el-menu-item>
              <el-menu-item index="4">服务器</el-menu-item>
            </el-menu-item-group>
          </el-menu>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  var md5Base64 = require('md5-base64');
  require('assets/css/style.css');
  export default {
    name: 'app',
    data(){
      return {
        showSide: false,
        hasLogin: false,
        dialogFormVisible: false,
        errorMsg: "",
        loading: false,
        loginBol: false,
        loginUserName: sessionStorage.getItem("loginUserName"),
        errorBol: false,
        form: {
          username: "",
          password: ""
        }
      }
    },
    methods: {
      login(){
        var checkRes = this.loginCheck();
        if (checkRes == true) {
          var randomNum = this.getRandom();
          this.loading = true;
          this.$http.jsonp(this.config.host + "/getRandom.do", {
            //修改持有人参数
            params: {
              nonce: randomNum
            },
            jsonp: 'jsoncallback'
          }).then(function (response) {
            let getRandomRes = response.data["success"];
            if (getRandomRes) {
              console.log("获取随机数成功");
              let reRandom = response.data["result"];
              this.$http.jsonp(this.config.host + "/login.do", {
                //修改持有人参数
                params: {
                  username: this.form.username,
                  password: md5Base64(randomNum+md5Base64(this.form.password)+reRandom)
                },
                jsonp: 'jsoncallback'
              }).then(function (response) {
                let loginRes = response.data["success"];
                sessionStorage.setItem("loginUserName", response.data.result["realname"]);
                if (loginRes) {
                  console.log("登录成功");
                  this.loading = false;
                  this.$store.state.loginBol = true;
                  this.loginUserName = sessionStorage.getItem("loginUserName");
                  this.cancel();
                } else {
                  //console.log("登录失败");
                  this.errorBol = true;
                  this.errorMsg = "登录失败";
                  this.loading = false;
                }
              }, function (response) {
                this.loading = false;
              })
            }else{
              console.log("随机数请求失败")
            }
          }, function (response) {
            console.log("随机数请求失败")
            this.loading = false;
          })
        }
      },
      loginCheck(){
        if (this.form.username == "") {
          this.errorMsg = "用户名不能为空";
          this.errorBol = true;
          return;
        }
        if (this.form.password == "") {
          this.errorMsg = "密码不能为空";
          this.errorBol = true;
          return;
        }
        return true;
      },
      clearError(){
        this.errorMsg = "";
        this.errorBol = false;
      },
      cancel(){
        this.clearError();
        this.dialogFormVisible = false;
        this.form.username = "";
        this.form.password = "";
      },
      exitLogin(){
        this.$store.state.loginBol = false;
        this.loginUserName = sessionStorage.clear("loginUserName");
        this.$message({
          message: "退出登录成功",
          type: "success",
          duration: 1200
        });
      },
      getRandom(){
        var Num = "";
        for (var i = 0; i < 6; i++) {
          Num += Math.floor(Math.random() * 10);
        }
        return Num;
      }
    },
    computed: mapState([
      'loginBol'
    ]),
    watch: {
      '$route': function (to, from) {

      }
    }
  }
</script>
<style>
  .el-loading-mask .el-loading-spinner {
    top: 50%;
  }
</style>



