import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as types from './mutation-types'

Vue.use(Vuex)

/* 测试数据 */
export default new Vuex.Store({
  actions,
  state: {
    count:0,
    num:5,
    keywords:"",
    loginBol:false,
    loginUser:"",
    filters:{
      "osCategory":"",
      "location":"",
      "state":""
    },
    filterBol:false,
    device_list:JSON.parse(sessionStorage.getItem("dlist"))?JSON.parse(sessionStorage.getItem("dlist")):[]
  },
  mutations: {
    [types.INCREMENT_COUNT](state,num){
      state.count+=num;
    },
    [types.CONDITION_FILTER](state,payload){
      state.filters[payload.condition]=payload.value;
      state.filterBol=true;
    },
    [types.CLEAR_FILTERS](state){
      "use strict";
      state.filterBol=false;
      let filters=state.filters;
      for(var key in filters){
        filters[key]="";
      }
    }
  }
})




